@import "../../assets/scss/mixins";

.footer {
  background-color: #245852;
  padding: 95px 100px 85px 100px;
  margin: 0 auto;

  h2 {
    font-family: "Avenir";
    font-size: 40px;
    line-height: 55px;
    color: #fff;
    margin: 0;
    padding-bottom: 60px;
  }

  nav {
    margin: 0 auto;
    max-width: 1370px;
    ul {
      padding: 0;
    }

    li {
      list-style: none;
      margin: 0;
      padding-bottom: 60px;
    }

    button {
      @include transparentButton(#fff);
      font-size: 25px;
      line-height: 30px;
      color: #fff;
    }
  }
}
